/* --------------------------------------------------------
-------------- Top Fixed Navigation Styling ---------------
----------------------------------------------------------*/
.top-nav {
    position: absolute;
    top: 30px;
    right: 20px;
    z-index: 100;
}

.top-nav li {
    font-family: Poppins;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1px;
    display: inline-block;
    margin-right: 25px;
}

.top-nav a {
    position: relative;
    display: inline-block;
    color: #111;
    text-decoration: none;
}

.top-nav a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    bottom: -3px;
    left: 0;
    background-color: #072DEF;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.2s ease-in-out 0s;
    transition: all 0.2s ease-in-out 0s;
}

.top-nav a:hover::before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
}

.no-touch .top-nav a:hover {
    text-decoration: none;
    color: #000;
}

